                    right: 0;\r\n                    top: 50%;\r\n                    transform: translateY(-50%);\r\n                    z-index: 1;\r\n                }\r\n\r\n                [class^=icon-] {\r\n                    display: inline-block;\r\n                    width: 48px;\r\n                    height: 48px;\r\n                    background-repeat: no-repeat;\r\n                    background-size: contain;\r\n                    margin-right: 15px;\r\n\r\n                    @media (max-width: 1485px) {\r\n                        width: 34px;\r\n                        height: 34px;\r\n                        margin-right: 10px;\r\n                    }\r\n                }\r\n\r\n                .icon-clue {\r\n                    background-image: url('~@/assets/images/work/icon-clue.png')\r\n                }\r\n\r\n                .icon-customer {\r\n                    background-image: url('~@/assets/images/work/icon-customer.png')\r\n                }\r\n\r\n                .icon-overtime {\r\n                    background-image: url('~@/assets/images/work/icon-overtime.png')\r\n                }\r\n\r\n                .icon-overdue {\r\n                    background-image: url('~@/assets/images/work/icon-overdue.png')\r\n                }\r\n            }\r\n\r\n            span {\r\n                font-size: 28px;\r\n                word-break: break-word;\r\n            }\r\n\r\n            @media (max-width: 1550px) {\r\n                font-size: 14px;\r\n                span {\r\n                    font-size: 20px;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.dcc-manage-component,\r\n.dcc-group-component {\r\n    .head-wrap {\r\n        color: #212427;\r\n\r\n        .service-num {\r\n            font-size: 16px;\r\n            color: $baseBlue;\r\n\r\n            span {\r\n                margin-left: 10px;\r\n            }\r\n        }\r\n    }\r\n\r\n    .pl20 {\r\n        padding-left: 20px!impor