8PoB,gBAAiB;CA9PrC;AAAA,sFAiQwB,cAAe;CAClB;CAlQrB;AAAA,kEA0QY,eAAgB;CA1Q5B;AAAA,4DA8QY,yBACA,aAAc;CA/Q1B;AAAA,uEAiRgB,kBACA,eACA,cACA,iBAAkB;CApRlC;AAAA,4EAuRoB,oBAAA,oBAAA,aACA,yBAAA,sBAAA,mBACA,yBAAA,sBAAA,8BACA,kBACA,aAAc;CA3RlC;AAAA,uFA8RwB,eAAgB;CA9RxC;AAAA,6FAkSwB,aAAc;CAlStC;AAAA,sFAsSwB,qBACA,gBACA,YACA,iBACA,gBACA,uBACA,oBACA,sBACA,eACA,aAAc;CA/StC;AAAA,sFAmTwB,qBACA,WACA,YACA,gBACA,oDACA,wBACA,sBACA,cAAe;CAClB;AAAA;AA3TrB,4DAgUgB,wBAAyB;CAhUzC;AAAA,uEAmUoB,kBACA,cAAe;CApUnC;AAAA,sFAwU4B,WACA,YACA,eAAgB;CA1U5C;AAAA,sFA8U4B,YACA,iBACA,cAAe;CAClB;CAjVzB;AAAA,qNA2VgB,WACA,8BAAA,qBAAsB;CA5VtC;AAAA,mPA8VoB,UACA,eACA,iBAAkB;CACrB;AAAA;AAjWjB,yLAqWgB,WAAY;CArW5B;AAAA,mPAuWoB,YACA,eACA,gBAAiB;CACpB;CA1WjB;AAAA,4MAiXY,UACA,mBACA,UACA,yCACA,gCAAiC;CArX7C;AAAA,8NAyXY,mBACA,uCACA,wCACA,+BAAgC;CACnC","file":"DccInvite.vue","sourcesContent":["// 全局配置颜色表\r\n$baseBlue: #3d7eff; // 广汽商贸集团的蓝色\r\n$basePlaceholder: #c0c4cc; // input placeholder颜色\r\n\r\n$themeColor: #21262b; // 主题色 el-menu \r\n$textColor: #2c314e; // 字体色  el-menu \r\n\r\n$levelB: #ab7df6;\r\n$levelH: #ff5a16;\r\n$levelC: #1494eb;\r\n$levelA: #d65aa8;\r\n$follow: #9d9fed;\r\n$green: #28c192;\r\n$orange: #fdae31;\r\n\r\n.blue {\r\n  color: $baseBlue\r\n}\r\n","@import \"src/assets/scss/mixin.scss\";\r\n\r\na {\r\n    font-size: 14px;\r\n    color: #3d7eff;\r\n}\r\n.head-wrap {\r\n    display: flex;\r\n    height: 50px;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 10px 15px;\r\n    color: $baseBlue;\r\n    font-size: 18px;\r\n    border-bottom: 1px solid #eaebf0;\r\n\r\n    @media (max-width: 1485px) {\r\n        height: 36px;\r\n        padding: 6px 10px;\r\n        font-size: 14px;\r\n    }\r\n}\r\n.card-box {\r\n    background-color: #ffffff;\r\n    border-radius: 4px;\r\n    border: 1px solid #eaebf0;\r\n}\r\n.card-stat {\r\n    .card-box {\r\n        display: flex;\r\n        flex-direction: column;\r\n        \r\n        .body-wrap {\r\n            flex: 1;\r\n            text-align: center;\r\n            font-size: 16px;\r\n\r\n            .cell {\r\n                position: relative;\r\n                display: flex;\r\n                align-items: center;\r\n                justify-content: center;\r\n                padding: 0 10px;\r\n                text-align: left;\r\n\r\n                &:not(:last-child)::after {\r\n                    content: \"\";\r\n                    width: 1px;\r\n                    height: 50%;\r\n                    border-right: 1px solid #eaebf0;\r\n                    position: absolute;\r\n                    right: 0;\r\n                    top: 50%;\r\n                    transform: translateY(-50%);\r\n                    z-index: 1;\r\n                }\r\n\r\n                [class^=icon-] {\r\n                    display: inline-block;\r\n                    width: 48px;\r\n                    height: 48px;\r\n                    background-repeat: no-repeat;\r\n                    background-size: contain;\r\n                    margin-right: 15px;\r\n\r\n                    @media (max-width: 1485px) {\r\n                        width: 34px;\r\n                        height: 34px;\r\n                        margin-right: 10px;\r\n                    }\r\n                }\r\n\r\n                .icon-clue {\r\n                    background-image: url('~@/assets/images/work/icon-clue.png')\r\n                }\r\n\r\n                .icon-customer {\r\n                    background-image: url('~@/assets/images/work/icon-customer.png')\r\n                }\r\n\r\n                .icon-overtime {\r\n                    background-image: url('~@/assets/images/work/icon-overtime.png')\r\n                }\r\n\r\n                .icon-overdue {\r\n                    background-image: url('~@/assets/images/work/icon-overdue.png')\r\n                }\r\n            }\r\n\r\n            span {\r\n                font-size: 28px;\r\n                word-break: break-word;\r\n            }\r\n\r\n            @media (max-width: 1550px) {\r\n                font-size: 14px;\r\n                span {\r\n                    font-size: 20px;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.dcc-manage-component,\r\n.dcc-group-component {\r\n    .head-wrap {\r\n        color: #212427;\r\n\r\n        .service-num {\r\n            font-size: 16px;\r\n            color: $baseBlue;\r\n\r\n            span {\r\n                margin-left: 10px;\r\n            }\r\n        }\r\n    }\r\n\r\n    .pl20 {\r\n        padding-left: 20px!important;\r\n    }\r\n\r\n    .staff-monitor {\r\n        .card-box {\r\n            border-bottom-left-radius: 0;\r\n            border-bottom-right-radius: 0;\r\n            border-bottom: 0;\r\n        }\r\n\r\n        .search-box {\r\n            padding: 30px 0 10px 20px;\r\n            display: flex;\r\n            /deep/ .search-lable {\r\n                width: auto;\r\n            }\r\n\r\n            @media (max-width: 1485px) {\r\n                padding: 16px 14px 0;\r\n\r\n                /deep/ .el-input--medium .el-input__inner {\r\n                    height: 32px;\r\n                    line-height: 32px;\r\n                }\r\n\r\n                /deep/ .el-button--medium.is-round {\r\n                    margin-left: 10px;\r\n                    padding: 9px 15px;\r\n                }\r\n            }\r\n        }\r\n      \r\n        .monitor-table {\r\n            background-color: #faf9fe;\r\n\r\n            .table-head span,\r\n            .table-body .row >div {\r\n                &:nth-child(1) {\r\n                    width: 48%;\r\n                }\r\n                &:nth-child(2) {\r\n                    width: 13%;\r\n                }\r\n                &:nth-child(3) {\r\n                    width: 13%;\r\n                }\r\n                &:nth-child(4) {\r\n                    width: 13%;\r\n                }\r\n                &:nth-child(5) {\r\n                    width: 13%;\r\n                }\r\n            }\r\n\r\n            .table-head {\r\n                display: flex;\r\n                background-color: #f1f5ff;\r\n                border: solid 1px #eeeeee;\r\n                padding: 10px 15px;\r\n                span {\r\n                    display: block;\r\n                    font-size: 16px;\r\n                    color: #212427;\r\n                    &:nth-child(1) {\r\n                        text-align: center;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .table-body {\r\n                max-height: 700px;\r\n                .row {\r\n                    display: flex;\r\n                    margin: 10px 0;\r\n                    height: 120px;\r\n                    padding: 15px;\r\n                    background-color: #ffffff;\r\n                    border-radius: 4px;\r\n                    border: solid 1px #eeeeee;\r\n\r\n                    &:last-child {\r\n                        margin-bottom: 0;\r\n                    }\r\n\r\n                    &>div {\r\n                        p {\r\n                            margin-bottom: 8px;\r\n                            color: #212427;\r\n\r\n                            label {\r\n                                font-size: 15px;\r\n                                padding-right: 5px;\r\n                                color: #929ca8;\r\n                            }\r\n\r\n                            span {\r\n                                font-size: 18px;\r\n\r\n                                &.overtime {\r\n                                    padding-left: 15px;\r\n                                    color: #ea5a54;\r\n                                }\r\n                            }\r\n                        }\r\n                    }\r\n\r\n                    .service-info {\r\n                        display: flex;\r\n                        &>div {\r\n                            &:nth-child(1) {\r\n 